clearfix\n\n// 简单垂直居中\n// ------------------------------\nvam()\n  @extend $vertical-align-middle\n\n// 文本溢出\n// @param 容器宽度\n// ------------------------------\ntruncate(width = 100%)\n  width width\n  overflow hidden\n  word-wrap normal\n  white-space nowrap\n  text-overflow ellipsis\n\n// 文本隐藏\n// ------------------------------\nhide-text()\n  overflow hidden\n  white-space normal\n  text-indent -9999px\n\n// 列表重置\n// ------------------------------\nreset-list()\n  list-style none\n  margin 0\n  padding 0\n\n// 占位符\n// @param 颜色\n// ------------------------------\nplaceholder(color = input-placeholder-color)\n  // placeholder\n  &::-moz-placeholder\n    color color\n    opacity 1!important\n\n  &:-ms-input-placeholder\n    color color\n\n  &::-webkit-input-placeholder\n    color color\n\n\n// 小三角\n// @param 颜色\n// @param 尺寸\n// @param 方向\n// ------------------------------\ntriangle(color = #000, size = 10px, direction = down)\n  @extend $triangle-core\n  border-width (size/2)\n\n  if direction is up\n    border-style dashed dashed solid\n    border-color transparent transparent color\n  else if direction is right\n    border-style dashed dashed dashed solid\n    border-color transparent transparent transparent color\n  else if direction is down\n    border-style solid dashed dashed\n    border-color color transparent transparent\n  else if direction is left\n    border-style dashed solid dashed dashed\n    border-color transparent color transparent transparent\n  else if direction is up-left\n    border-style solid dashed dashed solid\n    border-color color transparent transparent color\n  else if direction is up-right\n    border-style solid solid dashed dashed\n    border-color color color transparent transparent\n  else if direction is down-left\n    border-style dashed dashed solid solid\n    border-color transparent transparent color color\n  else\n    border-style dashed solid solid dashed\n    border-color transparent color color transparent\n\n/*\n * Size utility.\n *\n * Synopsis:\n *\n *   size: <width> <height> | <width & height>\n *\n * Examples:\n *\n *     size: 100% 30px\n *       yields:\n *         width: 100%\n *         height: 30px\n *\n *     size: 5px\n *       yields:\n *         width: 5px\n *         height: 5px\n *\n */\n\nsize()\n  if length(arguments) == 1\n    width: arguments[0]\n    height: arguments[0]\n  else\n    width: arguments[0]\n    height: arguments[1]\n\n// helper\n\n-pos(type, args)\n  i = 0\n  position: unquote(type)\n  for j in (1..4)\n    if length(args) > i\n      {args[i]}: args[i + 1] is a 'unit' ? args[i += 1] : 0\n    i += 1\n\n/*\n * Position utility.\n *\n * Synopsis:\n *\n *   fixed: <pos> [n] <pos> [n]\n *\n * Examples:\n *\n *     fixed: top left\n *     fixed: top 5px left\n *     fixed: top left 5px\n *     fixed: top 5px left 5px\n *\n */\n\nfixed()\n  -pos('fixed', arguments)\n\n/*\n * Position utility.\n *\n * Synopsis:\n *\n *   absolute: <pos> [n] <pos> [n]\n *\n * Examples:\n *\n *     absolute: top left\n *     absolute: top 5px left\n *     absolute: top left 5px\n *     absolute: top 5px left 5px\n *\n */\n\nabsolute()\n  -pos('absolute', arguments)\n\n/*\n * Position utility.\n *\n * Synopsis:\n *\n *   relative: <pos> [n] <pos> [n]\n *\n * Examples:\n *\n *     relative: top left\n *     relative: top 5px left\n *     relative: top left 5px\n *     relative: top 5px left 5px\n *\n */\n\nrelative()\n  -pos('relative', arguments)\n\n/**\n * 关闭图标基础样式\n */\nclose-base(theme, top = 0, icon-font-size = 14px)\n  font-size icon-font-size\n  color lookup(theme + '-close-default-color')\n  transition color @transition-time ease\n  position relative\n  top top\n  &:hover\n    color lookup(theme + '-close-hover-color')\n\n/**\n * 动画公用配置\n */\nmotion-common()\n  animation-duration animation-time\n  animation-fill-mode both\n\n/**\n * 动画\n * @param {String} class-name 类名\n * @param {String} keyframe-name 帧名\n */\nmake-motion(class-name, keyframe-name)\n  keyframeIn = keyframe-name + '-in'\n  keyframeOut = keyframe-name + '-out'\n  .{class-name}-enter-active\n    motion-common()\n    animation-play-state paused\n\n  .{class-name}-leave-active\n    motion-common()\n    animation-play-state paused\n\n  .{class-name}-enter-active\n    animation-name keyframeIn\n    animation-play-state running\n\n  .{class-name}-leave-active\n    animation-name keyframeOut\n    animation-play-state running\n"],"sourceRoot":""}]);

