let code = {}

code.base = `
<grid-layout
  :layout="layout1"
  :col-num="12"
  :row-height="rowHeight"
  :is-draggable="draggable"
  :is-resizable="resizable"
  :vertical-compact="true"
  :use-css-transforms="false">
  <grid-item
    class="demo-grid-item"
    v-for="item in layout1"
    :key="item.i"
    :x="item.x"
    :y="item.y"
    :w="item.w"
    :h="item.h"
    :min-w="2"
    :min-h="2"
    :i="item.i"
    @resize="onResize"
    @move="onMove"
    @resized="onResized"
    @moved="onMoved">
    <div class="num">
      {{ item.i }}
    </div>
  </grid-item>
</grid-layout>
`

export default code



