<template>
  <demo-article>
    <p>用于搜索</p>
    <example title="基础用法">
      <div slot="demo">
        <SearchBox
          :keyword.sync="query1"
          @search="onSearch">
        </SearchBox>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="带搜索类型">
      <div slot="demo">
        <SearchBox
          :type.sync="searchType"
          :typeOptions="searchTypeOptions"
          :keyword.sync="query2"
          @search="onSearch">
        </SearchBox>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/search-box'

export default {
  name: 'SearchBoxDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      searchType: 'id',
      searchTypeOptions: [{
        value: 'id',
        label: '设备ID'
      }, {
        value: 'mac',
        label: '设备MAC'
      }],
      query1: '',
      query2: '',
      code
    }
  },

  mounted () {
  },

  methods: {
    onSearch (keyword, type) {
      if (keyword === '') return
      if (type) {
        this.$Message.info(`搜索${keyword}, 类型${type}`)
      } else {
        this.$Message.info(`搜索${keyword}`)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'
</style>



