<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法" split="down">
      <div slot="demo">
        <div class="mb20">
          <x-button size="small" @click.native="decreaseWidth">减少容器宽度</x-button>
          <x-button size="small" @click.native="increaseWidth">增加容器宽度</x-button>
          <x-button size="small" @click.native="addItem" class="mr20">添加新项</x-button>
          行高: <x-input size="small" :width="60" v-model="rowHeight" number></x-input>
          <checkbox v-model="draggable" class="ml20">可拖拽</checkbox>
          <checkbox v-model="resizable" class="ml20">可调整尺寸</checkbox>
        </div>
        <div ref="content">
          <div class="grid-layout-info">
            <x-row>
              <x-col :span="4" v-for="item in layout1" :key="item">
                <span>{{item.i}}: [{{item.x}}, {{item.y}}, {{item.w}}, {{item.h}}]</span>
              </x-col>
            </x-row>
          </div>
          <GridLayout
            :layout="layout1"
            :col-num="12"
            :row-height="rowHeight"
            :is-draggable="draggable"
            :is-resizable="resizable"
            :vertical-compact="true"
            :use-css-transforms="false">
            <GridItem
              class="demo-grid-item"
              v-for="item in layout1"
              :key="item.i"
              :x="item.x"
              :y="item.y"
              :w="item.w"
              :h="item.h"
              :min-w="2"
              :min-h="2"
              :i="item.i"
              @resize="onResize"
              @move="onMove"
              @resized="onResized"
              @moved="onMoved">
              <div class="num">
                {{ item.i }}
              </div>
            </GridItem>
          </GridLayout>
        </div>
      </div>
      <div slot="desc">
        <p>组件 <code>&lt;GridLayout&gt;</code> 与 <code>&lt;GridItem&gt;</code> 配合使用</p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/grid-layout'

var layout = [
  { x: 0, y: 0, w: 2, h: 2, i: '0', resizable: true, draggable: true },
  { x: 2, y: 0, w: 2, h: 4, i: '1', resizable: null, draggable: null },
  { x: 4, y: 0, w: 2, h: 5, i: '2', resizable: false, draggable: false },
  { x: 6, y: 0, w: 2, h: 3, i: '3', resizable: false, draggable: false },
  { x: 8, y: 0, w: 2, h: 3, i: '4', resizable: false, draggable: false },
  { x: 10, y: 0, w: 2, h: 3, i: '5', resizable: false, draggable: false },
  { x: 0, y: 5, w: 2, h: 5, i: '6', resizable: false, draggable: false },
  { x: 2, y: 5, w: 2, h: 5, i: '7', resizable: false, draggable: false },
  { x: 4, y: 5, w: 2, h: 5, i: '8', resizable: false, draggable: false },
  { x: 6, y: 4, w: 2, h: 4, i: '9', resizable: false, draggable: false },
  { x: 8, y: 4, w: 2, h: 4, i: '10', resizable: false, draggable: false },
  { x: 10, y: 4, w: 2, h: 4, i: '11', resizable: false, draggable: false },
  { x: 0, y: 10, w: 2, h: 5, i: '12', resizable: false, draggable: false },
  { x: 2, y: 10, w: 2, h: 5, i: '13', resizable: false, draggable: false },
  { x: 4, y: 8, w: 2, h: 4, i: '14', resizable: false, draggable: false },
  { x: 6, y: 8, w: 2, h: 4, i: '15', resizable: false, draggable: false },
  { x: 8, y: 10, w: 2, h: 5, i: '16', resizable: false, draggable: false },
  { x: 10, y: 4, w: 2, h: 2, i: '17', resizable: false, draggable: false },
  { x: 0, y: 9, w: 2, h: 3, i: '18', resizable: false, draggable: false },
  { x: 2, y: 6, w: 2, h: 2, i: '19', resizable: false, draggable: false }
]

export default {
  name: 'GridLayoutDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      layout1: JSON.parse(JSON.stringify(layout)),
      draggable: true,
      resizable: true,
      rowHeight: 30,
      colNum: 0,
      index: 0,
      code
    }
  },

  mounted () {
    this.index = this.layout1.length
  },

  methods: {
    /**
     * 改变元素宽度
     * @param  { HTML DOM Element } el 目标元素
     * @param  { Number } w  增加或减少的宽度
     */
    _changeWidth (el, w) {
      let width = el.offsetWidth
      width += w
      el.style.width = `${width}px`
    },

    /**
     * 增加容器宽度
     */
    increaseWidth () {
      let el = this.$refs.content
      this._changeWidth(el, 20)
    },

    /**
     * 减少容器宽度
     */
    decreaseWidth (item) {
      let el = this.$refs.content
      this._changeWidth(el, -20)
    },

    /**
     * 移除项
     * @param  {Object} item 目标项
     */
    removeItem (item) {
      // this.layout.splice(this.layout.indexOf(item), 1)
    },

    /**
     * 增加项
     */
    addItem () {
      // console.log('### LENGTH: ' + this.layout.length)
      var item = {
        'x': 0,
        'y': 0,
        'w': 2,
        'h': 2,
        'i': this.index + '',
        whatever: 'bbb'
      }
      this.index++
      this.layout1.push(item)
    },

    /**
     * 处理移动
     * @param  { Number | String } i 索引
     * @param  { Number } newX 新横坐标
     * @param  { Number } newY 新纵坐标
     */
    onMove (i, newX, newY) {
      console.log('MOVE i=' + i + ', X=' + newX + ', Y=' + newY)
    },

    /**
     * 处理尺寸改动
     * @param  { Number | String } i 索引
     * @param  { Number } newH 新高度
     * @param  { Number } newW 新宽度
     */
    onResize (i, newH, newW) {
      console.log('RESIZE i=' + i + ', H=' + newH + ', W=' + newW)
    },

    /**
     * 处理已移动
     * @param  { Number | String } i 索引
     * @param  { Number } newX 新横坐标
     * @param  { Number } newY 新纵坐标
     */
    onMoved (i, newX, newY) {
      console.log('### MOVED i=' + i + ', X=' + newX + ', Y=' + newY)
      this.$Notice.warning({
        title: '检测到区块位置改动',
        desc: `区块 ${i} 移动到坐标 [${newX}, ${newY}]`
      })
    },

    /**
     * 处理尺寸已改动
     * @param  { Number | String } i 索引
     * @param  { Number } newH 新高度
     * @param  { Number } newW 新宽度
     * @param  { Number } newHPx 新高度像数值
     * @param  { Number } newWPx 新宽度像数值
     */
    onResized (i, newH, newW, newHPx, newWPx) {
      console.log('### RESIZED i=' + i + ', H=' + newH + ', W=' + newW + ', H(px)=' + newHPx + ', W(px)=' + newWPx)
      this.$Notice.warning({
        title: '检测到区块尺寸改动',
        desc: `区块 ${i} 尺寸变成 [${newW}, ${newH}]，像素值为[${newWPx}, ${newHPx}]`
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.demo-grid-item
  display table

  .num
    display table-cell
    vertical-align middle
    text-align center
    font-size 36px
    opacity .1

.theme-light
  .demo-grid-item
    background #F3F3F3
.theme-dark
  .demo-grid-item
    background #292929
</style>



