<template>
  <demo-article>
    <p>表单组件</p>
    <example title="基础用法">
      <div slot="demo">
        <XForm label-width="100px" label-position="right" :model="form" :rules="rules" ref="form">
          <XFormItem label="姓名" prop="name">
            <x-input v-model="form.name" :width="200"></x-input>
          </XFormItem>
          <XFormItem label="省份" prop="province">
            <x-select v-model="form.province" style="width: 200px;">
              <x-option v-for="item in provinces" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </XFormItem>
          <XFormItem label="级联选择" prop="cascader">
            <cascader style="width: 200px;" :data="spots" v-model="form.cascader"></cascader>
          </XFormItem>
          <XFormItem label="单选框组" prop="radios">
            <radio-group v-model="form.radios">
              <radio label="IOS"></radio>
              <radio label="Android"></radio>
              <radio label="Windows"></radio>
            </radio-group>
          </XFormItem>
          <XFormItem label="复选框" prop="mutiple">
            <checkbox v-model="form.mutiple">复选框一</checkbox>
          </XFormItem>
          <XFormItem label="复选框组" prop="mutipleGroup">
            <checkbox-group v-model="form.mutipleGroup">
              <checkbox label="iOS"></checkbox>
              <checkbox label="Android"></checkbox>
              <checkbox label="Windows"></checkbox>
            </checkbox-group>
          </XFormItem>
          <XFormItem label="日期选择器" prop="date">
            <date-picker
              style="width: 200px"
              type="date"
              placeholder="选择日期"
              v-model="form.date">
            </date-picker>
          </XFormItem>
          <XFormItem label="时间选择器" prop="time">
            <time-picker
              style="width: 168px;"
              type="time"
              placeholder="选择时间"
              v-model="form.time">
            </time-picker>
          </XFormItem>
          <XFormItem label="开关" prop="switch">
            <x-switch v-model="form.switch"></x-switch>
          </XFormItem>
          <XFormItem label="描述" prop="discription">
            <x-input v-model="form.discription" type="textarea"></x-input>
          </XFormItem>
          <XFormItem>
            <x-button type="primary" @click.native="submitForm">提交</x-button>
            <x-button>取消</x-button>
          </XFormItem>
        </XForm>
      </div>
      <div slot="desc">
        copy from <a src="http://element.eleme.io/#/zh-CN/component/form">element ui</a>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
      <x-code lang="js" slot="code">{{ code.baseData }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/form'

export default {
  name: 'FormDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        discription: [
          { required: true, message: '请输入描述', trigger: 'blur' },
          { min: 3, max: 14, message: '长度在 3 到 14 个字符', trigger: 'blur' }
        ],
        province: [
          { required: true, message: '请输入省份', trigger: 'change' }
        ],
        radios: [
          { required: true, message: '请选择一个', trigger: 'change' }
        ],
        mutiple: [
          { required: true, message: '请输入一个', trigger: 'change', type: 'boolean' }
        ],
        mutipleGroup: [
          { required: true, message: '请输入系统', trigger: 'change', type: 'array' },
          { validator: this.customMutipleValidate, trigger: 'change' }
        ],
        date: [
          { required: true, message: '请输入日期', trigger: 'change' }
        ],
        time: [
          { required: true, message: '请输入时间', trigger: 'change' }
        ],
        cascader: [
          { required: true, trigger: 'change', type: 'array' },
          { validator: this.customValidate, trigger: 'change' }
        ],
        switch: [
          { required: true, trigger: 'change', type: 'boolean' }
        ]
      },
      form: {
        name: '',
        province: '',
        radios: 'IOS',
        mutiple: true,
        mutipleGroup: [],
        switch: true,
        date: new Date(),
        time: new Date(),
        discription: '',
        cascader: [{
          value: 'beijing',
          label: '北京'
        }, {
          value: 'tiantan',
          label: '天坛'
        }]
      },
      spots: [{
        value: 'beijing',
        label: '北京',
        children: [{
          value: 'gugong',
          label: '故宫'
        }, {
          value: 'tiantan',
          label: '天坛'
        }, {
          value: 'wangfujing',
          label: '王府井'
        }]
      }, {
        value: 'jiangsu',
        label: '江苏',
        children: [{
          value: 'nanjing',
          label: '南京',
          children: [{
            value: 'fuzimiao',
            label: '夫子庙'
          }]
        }, {
          value: 'suzhou',
          label: '苏州',
          children: [{
            value: 'zhuozhengyuan',
            label: '拙政园'
          }, {
            value: 'shizilin',
            label: '狮子林'
          }]
        }]
      }],
      provinces: [{
        value: '',
        label: '无'
      }, {
        value: 'anhui',
        label: '安徽'
      }, {
        value: 'shanghai',
        label: '上海'
      }]
    }
  },
  methods: {
    submitForm () {
      this.$refs.form.validate()
    },
    customValidate (rule, value, callback) {
      if (!value.length) {
        callback(new Error('请输入所在区域'))
      } else if (value.length === 1) {
        callback(new Error('请输入市/区'))
      }
    },
    customMutipleValidate (rule, value, callback) {
      if (!value.length) {
        callback(new Error('请至少选择一项'))
      }
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



